.container {

  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;

  @media screen and (min-width: 414px) {
    width: var(--mobile);
  }

  @media screen and (min-width: 1024px) {
    width: var(--tablet);
    padding-left: 47px;
    padding-right: 47px;
  }

  @media screen and (min-width: 1280px) {
    width: var(--desktop);
    padding-left: 85px;
    padding-right: 85px;
  }
}